import "./input.css";
export function Input() {
  return (
    <>
      <h1> INPUT </h1>
      <p>
        A basic widget for getting the user input is a text field. Keyboard and
        mouse can be used for providing or changing data.
      </p>
      <p>When To Use </p>
      <li className="head">A user input in a form field is needed.</li>
      <li className="head">A search input is required.</li>
      <h2>Sign-up Form</h2>
      <div className="box">
        <div className="form">
          <form>
            <fieldset>
              <legend>FirstName</legend>
              <input placeholder="write your first name" />
            </fieldset>
            <fieldset>
              <legend>LastName </legend>
              <input placeholder="write your lastName" />
            </fieldset>
            <fieldset>
              <legend>Email-id </legend>
              <input placeholder="write your E-mail" />
            </fieldset>
            <fieldset>
              <legend>Create Password </legend>
              <input placeholder="create password" />
            </fieldset>
            <fieldset>
              <legend>Confirm Password </legend>
              <input placeholder="confirm password" />
            </fieldset>

            <button type="submit">Register</button>
          </form>
        </div>
      </div>

      <h2>Login-form</h2>

      <div className="box">
        <div className="form">
          <form>
            <fieldset>
              <legend>E-mail </legend>
              <input />
            </fieldset>
            <fieldset>
              <legend>Password </legend>
              <input />
            </fieldset>

            <button>Login</button>
          </form>
        </div>
      </div>
      <div className="box">
        <fieldset>
          <legend className="legend">Check </legend>

          <form class="w3-container w3-card-4">
            <p>
              <input class="w3-check" type="checkbox" checked="checked" />
              <label>Milk</label>
            </p>
            <p>
              <input class="w3-check" type="checkbox" />
              <label> Sugar</label>
            </p>
            <p>
              <input class="w3-check" type="checkbox" disabled />
              <label>Lemon (Disabled)</label>
            </p>
          </form>
        </fieldset>
        <fieldset>
          <legend className="legend">Radio</legend>

          <form class="w3-container w3-card-4">
            <p>
              <input
                class="w3-radio"
                type="radio"
                name="gender"
                value="male"
                checked
              />
              <label>Male</label>
            </p>
            <p>
              <input
                class="w3-radio"
                type="radio"
                name="gender"
                value="female"
              />
              <label>Female</label>
            </p>
            <p>
              <input
                class="w3-radio"
                type="radio"
                name="gender"
                value=""
                disabled
              />
              <label>Don't know (Disabled)</label>
            </p>
          </form>
        </fieldset>
      </div>
    </>
  );
}
