import "./card.css";
import { IoHeartOutline } from "react-icons/io5";
import { IoShareSocialOutline } from "react-icons/io5";
import { IoEllipsisVerticalSharp } from "react-icons/io5";
export function Card() {
  return (
    <>
      <h1> CARD</h1>
      <p>
        Cards are surfaces that display content and actions on a single
        topic.They should be easy to scan for relevant and actionable
        information. Elements, like text and images, should be placed on them in
        a way that clearly indicates hierarchy .{" "}
      </p>
      <h2>text-Card</h2>
      <p>A text card containing a title, content and an extra corner content</p>
      <div className="box">
        <div className="container">
          <div className="price">WORD OF THE DAY</div>
          <div className="head">Intemperate</div>
          <div className="tag">adjective || in-TEM-puh-rut</div>
          <div className="desc">
            Intemperate means "having extreme conditions" or "having or showing
            a lack of emotional calmness or control."
          </div>
        </div>
      </div>
      <h2>media-Card</h2>
      <p>
        Example of a card using an image to reinforce the content.Often a card
        allow users to interact with the entirety of its surface to trigger its
        main action, be it an expansion, a link to another screen or some other
        behavior.
      </p>
      <div className="box">
        <div className="container">
          <img
            src="https://via.placeholder.com/300x100"
            width="290"
            height="70"
          />
          <div className="head">Our Changing Planet</div>
          <div className="tag"> by Kurt Wegnar </div>
          <div className="desc">
            {" "}
            Visit ten places on our planet that are undergoing the biggest
            changes today
          </div>
          <div className="f-box ff">
            <a href="/">READ</a>
            <a href="/">BOOKMARK</a>
            <div>
              <IoHeartOutline />
            </div>
            <IoShareSocialOutline />
            <IoEllipsisVerticalSharp />
          </div>
        </div>
      </div>
      <h2>horizontal-Card</h2>
      <p>horizontal media card</p>
      <div className="box">
        <div className="container">
          <div className="f-box">
            <img src="https://via.placeholder.com/100x100" />
            <div className="f-box ht">
              <div className="head">Our Changing Planet</div>
              <div className="tag"> by Kurt Wegnar </div>
            </div>
          </div>
          <div className="f-box">
            <a href="/">READ</a>
            <a href="/">BOOKMARK</a>
            <div>
              <IoHeartOutline />
            </div>
            <IoShareSocialOutline />
            <IoEllipsisVerticalSharp />
          </div>
        </div>
      </div>{" "}
      <h2>text-over-Image</h2>
      <p>
        CSS position property is used to set the position of text over an
        image.The absolute elements are positioned relative to their parent.
      </p>
      <div className="box">
        <div className="container">
          <img src="https://via.placeholder.com/300x130" />
          <div className="head hof">Our Changing Planet</div>
          <div className="tag tof "> by Kurt Wegnar </div>
          <div className="desc">
            {" "}
            Visit ten places on our planet that are undergoing the biggest
            changes today
          </div>
          <div className="f-box ff">
            <a href="/">READ</a>
            <a href="/">BOOKMARK</a>
            <div>
              <IoHeartOutline />
            </div>
            <IoShareSocialOutline />
            <IoEllipsisVerticalSharp />
          </div>
        </div>
      </div>
      <h2>product-Card</h2>
      <p>
        Product card has image, product tiitle, tag, small decription about
        product. it allows users to interact with buttons to add into cart or
        add into wishlist.
      </p>
      <div className="box">
        <div className="container">
          <img
            height="210px"
            width="190px"
            src="https://via.placeholder.com/300x130"
          />
          <div className="head">Apple</div>
          <div className="tag">2nd Gen AirPods</div>
          <div className="f-box">
            <div className="price"> Rs.11999 </div>
            <div className="c-price"> Rs.13999 </div>
            <div className="cut"> ---- </div>
            <div className="off"> (30% OFF) </div>
          </div>
          <button>ADD TO CART</button>
        </div>
      </div>
      <h2>badge-card</h2>
      <p>
        We can set a badge over the image of the product, CSS position property
        is used to set the position of badge over an image.
      </p>
      <div className="box">
        <div className="container">
          <img
            height="210px"
            width="190px"
            src="https://via.placeholder.com/300x130"
          />
          <div className="head">Apple</div>
          <div className="OB" style={{ border: "1px solid black" }}>
            NEW
          </div>
          <div className="tag">2nd Gen AirPods</div>
          <div className="f-box">
            <div className="price"> Rs.11999 </div>
            <div className="c-price"> Rs.13999 </div>
            <div className="cut"> ---- </div>
            <div className="off"> (30% OFF) </div>
          </div>
          <button>ADD TO CART</button>
        </div>
      </div>
    </>
  );
}
