import "./toast.css";
export function Toast() {
  return (
    <>
      <h1> SnackBar/Toast </h1>
      <p>
        Snackbars are often used as tooltips/popups to show a message at the
        bottom of the screen.
      </p>
      <h2>Basic</h2>
      <div className="box b">
        <div className="head">
          Click on the button to show the snackbar. It will disappear after 3
          seconds
        </div>
        <button>Show SnackBar</button>
      </div>

      <h2>Add to Cart</h2>
      <div className="box b">
        <div className="head">
          Click on the button to show the snackbar. It will disappear after 3
          seconds
        </div>
        <button>ADD TO CART</button>
      </div>

      <h2>Remove</h2>
      <div className="box b">
        <div className="head">
          Click on the button to show the snackbar. It will disappear after 3
          seconds
        </div>
        <button>REMOVE</button>
      </div>
    </>
  );
}
